  import { Card, CardBody } from "react-bootstrap";
  import AssetsTypeCountChart from "../../layout/common/graph/assetsTypeCountChart";
  import React from "react";
  import ImageSkeleton from "../../layout/skeleton/imageSkeleton";
  import SectionHeading from "../../layout/common/sectionHeading";

  const PosttypeCount = (props) => {
    const { postTypeCount, loader } = props;

    return (
      <Card className="sw-card-custom gutter-b card-shadow mb-4">
        <SectionHeading  section={2}  title={'Post Type Count'} />
        <CardBody className="p-5">
          {loader ? (
            <ImageSkeleton img={"post-count"} />
          ) : postTypeCount && Object.keys(postTypeCount).length ? (
            <AssetsTypeCountChart key={loader} {...props} />
          ) : null}
        </CardBody>
      </Card>
    );
  };

  export default React.memo(PosttypeCount);
